<template>
  <div class="project">
    <div class="container">
      <span class="tip">WHAT I DO</span>
      <h1 class="title">{{title}}</h1>
      <span class="border"></span>
      <ul>
        <li v-for="(item,i) in projects" :key="i">
          <a :href="item.link" target="_blank">
            <div class="project-desc">
              <div class="project-text" >
                <p class="project-name">{{item.name}}</p>
                <p class="project-info">{{item.info}}</p>
              </div>
            </div>
          </a>
          <img :src="item.imgUrl">
        </li>

        <div class="clearfix"></div>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'project',
  data () {
    return {
      title: '',
      projects:[//
        {
          name:"",
          info:"HR",
          imgUrl:require('../common/images/resume.png'),
          link:"https://jiesue.github.io/resume"
        },
        {
          name:"",
          info:"HTMLCSS",
          imgUrl:require('../common/images/simpleBlog.png'),
           link:"https://jiesue.github.io/simpleBlog"
        },
        {
          name:"",
          info:"HTMLCSSjs",
          imgUrl:require('../common/images/suning.png'),
           link:"https://jiesue.github.io/suning"
        },
        {
          name:"",
          info:"js",
          imgUrl:require('../common/images/pushBoxGame.png'),
           link:"https://jiesue.github.io/pushBoxGame"
        }
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .project{height: auto;width: 100%;margin: 0 auto;background-color: #fff;text-align: center;}
  .container{height: auto;max-width: 1200px;margin: 0 auto;padding: 4% 0;}
  .tip{display:inline-block;position:relative;padding:6px 20px;margin:0 auto 10px;font-size: 20px;background:#03a9f4;color: #fff;}
  .tip:after{position:absolute;width: 0;height: 0;left:42%;bottom:-10px;content: '';border-style: solid;border-width: 10px 10px 0 10px;border-color: #03a9f4 transparent;}
  .title{padding: 6px 0 10px;font-family: 'Overlock', cursive;color: #2F2C06;font-size: 36px;}
  .border{display:block;width: 20%;height: 1px;position:relative;margin:0 auto 30px;background: #8C8989;}
  .border:after{position: absolute;top: 0;left: 30%;content: " ";width: 40%;height: 5px;margin-top:-2px;background: #03a9f4;}

  ul{width: 100%;height:350px;padding: 4% 0;}
  ul li{float: left;width: 20%;margin-right: 5%;height: 80%;box-sizing: border-box;transition: transform .25s ease;position: relative;}
  
  .project-desc{ position: absolute;left: 0; top: 0;width: 100%;height: 100%;background: rgba(100,50,200,.7);color:#fff;overflow: hidden;opacity: 0;transition: .0s;}
   .project-desc:hover{opacity: 1;}
  .project-text{display:inline-block;width:80%;height: 50%;word-wrap: break-word;margin-top: 40%;}
  .project-title{font-size: 18px;font-weight: bold;}
  .project-info{text-align: start; font: 12px;color:#94cee9}
  ul li img{display: block;width: 100%;height: 100%;}
  ul li:hover{z-index: 100; -webkit-transform: scale(1); transform: scale(1); box-shadow: 0 8px 17px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);border:1px solid red;} 
</style>



