<template>
  <div class="home">
  	<top>
  		<vue-particles
  		  color="#ffffff"
  		  :particleOpacity="0.6"
  		  linesColor="#ffffff"
  		  :particlesNumber="90"
  		  shapeType="circle"
  		  :particleSize="5"
  		  :linesWidth="2"
  		  :lineLinked="true"
  		  :lineOpacity="0.4"
  		  :linesDistance="2"
  		  :moveSpeed="3"
  		  :hoverEffect="true"
  		  hoverMode="grab"
  		  :clickEffect="true"
  		  clickMode="push"
  		>
  		</vue-particles>
		    <!-- 
			color: String'#dedede'
			particleOpacity: Number0.7
			particlesNumber: Number80
			shapeType: String'circle'"circle","edge","triangle", "polygon","star"
			particleSize: Number80
			linesColor: String'#dedede'
			linesWidth: Number1
			lineLinked: true
			lineOpacity: Number0.4
			linesDistance: Number150
			moveSpeed: Number3
			hoverEffect: truehover
			hoverMode: Stringtruehover: "grab", "repulse", "bubble"
			clickEffect: trueclick
			clickMode: Stringtrueclick: "push", "remove", "repulse", "bubble" 
			-->
  	</top>
  
  	<experience/>
  	<skill/>
  	<project/>
		<about/>
  	<bottom/>
  </div>
</template>

<script>
import top from '@/components/top'
import bottom from '@/components/bottom'
import about from '@/components/about'
import experience from '@/components/experience'
import skill from '@/components/skill'
import project from '@/components/project'
export default {
  name: 'home',
  data () {
    return {
    }
  },
  components:{
  	top,
  	bottom,
  	about,
  	experience,
  	skill,
  	project
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.home{
		min-width: 1200px;
		height: auto;
	}
</style>




